import common from "~/utils/common";
import jsCookie from "@/utils/js-cookie";
export default {
  name: "guestDialog",
  props: {
    guestDialogVisible: {
      type: Boolean
    },
    sku_id: {
      type: String
    },
    goods_id: {
      type: String
    },
    quantity: {
      type: Number
    },
    type: {
      type: Number
    },
    cartIds: {
      type: String
    },
    subtotal: {
      type: Number
    }
  },
  data() {
    return {
      guestCheckoutLoading: false
    };
  },
  mounted() {
    console.log(this.cartIds, "cartIds", this.type);
    this.$store.dispatch("actGuestDialogVisible", false);
    jsCookie.set("guest", false, {
      expires: 7,
      path: "/"
    });
  },
  methods: {
    handleClose() {
      this.$store.dispatch("actGuestDialogVisible", false);
      jsCookie.set("guest", false, {
        expires: 7,
        path: "/"
      });
    },
    guestCheckout(type) {
      this.guestCheckoutLoading = true;
      //游客直接购买
      if (type == 2) {
        this.$apis
          .buyNow(
            {
              quantity: this.quantity,
              goods_id: this.goods_id,
              sku_id: this.sku_id
            },
            this.$store.getters.getApiHeader
          )
          .then(res => {
            this.guestCheckoutLoading = false;
            if (res.status == 1) {
              this.$store.dispatch("actGuestDialogVisible", true);
              jsCookie.set("guest", true, {
                expires: 7,
                path: "/"
              });
              this.$router.push({
                path: "/checkoutDetail"
              });
            } else if (res.status == 14001) {
              this.$emit("noStockFn", false);
              this.$store.dispatch("actGuestDialogVisible", false);
              jsCookie.set("guest", false, {
                expires: 7,
                path: "/"
              });
            }
          })
          .catch(err => {
            this.guestCheckoutLoading = false;
            this.$store.dispatch("actGuestDialogVisible", false);
            jsCookie.set("guest", false, {
              expires: 7,
              path: "/"
            });
            console.log(err);
          });
      } else {
        //游客checkout第一步
        if (this.cartIds) {
          this.checkoutStepOne(this.cartIds);
        }
      }
    },
    checkoutStepOne(str) {
      let thif = this;
      this.$apis
        .checkoutStepOne(
          {
            step: "1",
            cart_ids: str
          },
          this.$store.getters.getApiHeader
        )
        .then(res => {
          this.guestCheckoutLoading = false;
          if (res.status == 1) {
            this.$router.push({
              path: "/checkoutDetail"
            });
          }
        })
        .catch(err => {
          this.guestCheckoutLoading = false;
          console.log(err);
        });
    },
    sign() {
      console.log(this.$route)
      let {name} = this.$route;
      this.$router.push({
        path: "/login",
        query: {
          skuIds: this.sku_id,
          subtotal: this.subtotal,
          from: name
        }
      });
    }
  }
};
