let vm = null;
import listCard from "@/components/listCard/listCard";
export default {
  name: "mayLike",
  props: {
    types: {
      type: Number,
      value: 1
    }
  },
  data() {
    return {
      testImg:
        "https://cdn.foroo.co.uk/wp-content/uploads/2020/03/315531200569540608.jpg",
      recommendArr: [],
      progress : 0,
      arrowRight: true,
      swiperOption: {
        slidesPerView: 4,
        slidesPerGroup: 4,
        spaceBetween: 32,
        slideClass: "custom-slide-class",
        //设置点击箭头
        paginationClickable: true,
        navigation: {
          prevEl: ".el-icon-arrow-left",
          nextEl: ".el-icon-arrow-right"
        },
        //开启无限循环
        loop: true,
        on: {
          slideChange: () => {
            // 通过$refs获取对应的swiper对象
            let { swiper } = this.$refs.mySwiper;
            vm.progress = swiper.progress;
          }
        }
      }
    };
  },
  created() {
    vm = this;
  },
  mounted() {
    if (this.types == 3) {
      this.getCartRecommend();
    } else if (this.types == 2) {
      this.getProductRecommend();
    }
  },
  computed: {
    changeWidth() {
      return this.types == 3;
    }
  },
  methods: {
    slidePrev(){
      let { swiper } = this.$refs.mySwiper;
      swiper.slidePrev();
      this.progress = swiper.progress;
    },
    slideNext(){
      let { swiper } = this.$refs.mySwiper;
      swiper.slideNext();
      this.progress = swiper.progress;
    },
    getCartRecommend() {
      let thif = this;
      this.$apis
        .mayLikeShop({ page: 1, per_page: 12, type: 3 })
        .then(res => {
          // console.log(res);
          if (res.status == 1) {
            thif.recommendArr = res.data.items;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    getProductRecommend() {
      //刷新页面取不到父组件传的id换成$route.params传参
      let { id } = this.$route.params;
      let thif = this;
      this.$apis
        .mayLikeShop({ page: 1, per_page: 16, goods_id: id, type: 2 })
        .then(res => {
          if (res.status == 1) {
            thif.recommendArr = res.data.items;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    toShopDetail(id, name) {
      this.buryingPoint.productClick(id, name);
      this.$router.push({
        path: "/product/" + id
      });
    }
  }
};
